body {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, sans-serif;
}

input {
  max-width: 500px;
}

.gray {
  color: #828282;
}

.orange {
  background-color: #3b83bd;
}

.background-gray {
  background-color: rgb(246, 246, 239);
}

.f11 {
  font-size: 11px;
}

.w85 {
  width: 85%;
}

.button {
  font-family: monospace;
  font-size: 10pt;
  color: black;
  background-color: skyblue;
  border-radius: 60px;
  text-align: center;
  padding: 2px 6px 3px;
  border-width: 2px;
  border-style: outset;
  border-color: blue;
  cursor: pointer;
  max-width: 250px;
}

.button:hover {
  color: blue;
  font-size: 11pt;
}

.i-Home{
  font-size: 2rem;
  color: darkorange;
}

.Center{
  text-align: center;
  font-size: 1rem;
}